import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';

//To redirect bring in the router
import {Router} from '@angular/router';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name:String;
  username: String;
  email:String;
  password:String;


  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
    ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

      //Required Fields
    if (!this.validateService.validateRegister(user)){
        //console.log('Fill value');
        this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeour:3000});
        return false;
    }

      //Validate Email
      if (!this.validateService.validateEmail(user.email)){
        //console.log('Fill value email correctly');
        this.flashMessage.show('Please use a valid email', {cssClass: 'alert-danger', timeour:3000});
        return false;
    }

    //Register User
    this.authService.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You are now registered and can log in', {cssClass: 'alert-success', timeour:3000});
        this.router.navigate(['/login'])
      }else{
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeour:3000});
        this.router.navigate(['/register'])
      }
    });

  }



}



